<template>
 <section>
      <my-header></my-header>
      <el-container>
        <el-aside width="200px">
          <el-col>
            <el-menu default-active="1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose">
              <el-menu-item index="1">
                <i class="el-icon-menu"></i>
                <span slot="title"></span>
              </el-menu-item>
            </el-menu>
          </el-col>
        </el-aside>
        <el-container>
          <el-main>
            <el-card>
              <h1></h1>
              <article></article>
              <p> 2018-10-18 20:16:27</p>
            </el-card>
          </el-main>
        </el-container>
      </el-container>
      <el-footer style="  height: 30px;">Designed & Powerd by Wallace Chan
        Copyright 2018 
      </el-footer>
</section>
</template>
<script>
import myHeader from './header.vue'
export default {
  components: {
    'my-header': myHeader
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    }
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.el-container {
  height: 100%
}

.el-footer {
  font-size: 12px;
  color: #fff;
  background-color: @mainColor;
  line-height: 30px;
}

.el-header,
.el-footer {
  text-align: center;
}

.el-aside {
  background-color: #D3DCE6;
  color: #333;
  text-align: center;
  line-height: 200px;
}

body>.el-container {
  margin-bottom: 40px;

}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}

.el-card {
  cursor: pointer;
  // background-color: #fff;
  // margin-bottom: 10px;
  // border-radius: 2px;
  // box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
  // padding: 10px 20px;

  h1 {
    font-size: 24px;
    font-weight: 300;
    color: @mainColor;
  }

  article {
    color: @gary;
    margin-top: 10px;
    line-height: 24px;
    overflow: hidden;
    display: -webkit-box;
    text-overflow: ellipsis;
    -webkit-line-clamp: 4;
    /*! autoprefixer: off */
    -webkit-box-orient: vertical;
    /*! autoprefixer: on */
  }

  p {
    font-size: 12px;
    text-align: right;
    margin-top: 5px;
  }
}

</style>



